@use 'base/variables' as v;
@use 'base/mixins' as m;

.propiedades{
    margin-top: 5rem;
    @include m.tablet{
        margin-top: 10rem;
    }

    &__heading{
        @include m.heading;
    }


    &__grid{
        @include m.grid(1, 2rem);
        @include m.tablet{
        @include m.grid(2, 2rem);
        }
    }
}

.propiedad{
    background-color: v.$blanco;

    &__imagen{
        aspect-ratio: 1 / 1;
        position: relative;
    }

    @for $i from 1 through 6 {
        $imagen: '../img/promo_'+ $i + '.png';
        &:nth-child(#{$i}) &__imagen{
            background-image: url($imagen);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
        }
    }

    &__texto-1{
        position: absolute;
        left: 1rem;
        top: 2rem;
        color: v.$primario;
        font-size: 2rem;
        @include m.desktopXL{
            font-size: 3rem;
        }
    }

    &__texto-2{
        position: absolute;
        right: 1rem;
        top: 2rem;
        color: v.$primario;
        font-size: 2rem;
        @include m.desktopXL{
            font-size: 3rem;
        }
    }
    &__bloque-txt-1{
        position: absolute;
        top: 7rem;
        left: 2rem;
        @include m.desktopXL{
            top: 15rem;
        }
    }
    &__bloque-txt-1 p{
        color: v.$primario;
        font-family: v.$fuenteSecundaria;
        font-weight: 700;
        font-size: 1.2rem;
        @include m.desktopXL{
            font-size: 1.8rem;
        }
    }
    &__bloque-txt-2{
        position: absolute;
        top: 7rem;
        right: 1rem;
        @include m.desktopXL{
            top: 15rem;
            right: 2rem;
        }
    }
    &__bloque-txt-2 p{
        color: v.$primario;
        font-family: v.$fuenteSecundaria;
        font-weight: 700;
        font-size: 1.2rem;
        @include m.desktopXL{
            font-size: 1.8rem;
        }
    }

    &__contenido{
        padding: 1rem;

    }
    &__texto{
        margin-top: 0;
        margin-bottom: 0;

    }
    &__precio{
        font-size: 3rem;
        font-weight: 900;
        margin: 0;
        color: v.$primario;
    }
    &__enlace{
        @include m.boton(v.$primario, v.$blanco);
        display: block;
        text-transform: uppercase;
    }
    &__aviso{
        color: #7c7c7c;
        text-align: center;
    }
    
    &__redirigir{
        display: block;
        margin: 0 auto;
        width: 70%;
        text-align: center;
        margin-top: 2rem;
        font-size: 1.8rem;
        font-weight: 700;
    }
    &__redirigir span{
        @include m.boton(v.$primario, v.$blanco);
        text-transform: uppercase;
    }
}