@use "base/variables" as v;
@use "base/mixins" as m;

#btnAbrirModal {
  background-color: darken(v.$primario, 10%);
  border: none;
  color: v.$blanco;
  font-weight: 700;
  padding: 0 2rem 2rem;
  border-radius: 0.5rem;
  cursor: pointer;
  z-index: 100;
  position: fixed;
  top: 50%;
  left: -60px;
  rotate: 90deg;
  transition: left 0.5s ease-in;
}
#btnAbrirModal:hover {
  left: -50px;
}

/* Estilo para el modal */
.modal {
  z-index: 100;
  display: none; /* El modal estará oculto inicialmente */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Fondo semitransparente */
}
.modal-titulo{
  text-align: center;
  color: v.$primario;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}

/* Estilo para el contenido del modal */
.modal-contenido {
  z-index: 100;
  background-color: #fff;
  border-radius: .5rem;
  margin: 10% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 400px;
  @include m.tablet{
    max-width: 700px;
  }
  position: relative;
}

/* Estilo para el botón de cierre del modal */
.cerrar-modal {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.cerrar-modal:hover,
.cerrar-modal:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

/* Estilo para la galería de imágenes */
.galeria {
  display: flex;
  justify-content: center;
  align-items: center;
}

.galeria img {
  border-radius: .5rem;
  max-width: 100%;
  height: 50rem;
  display: none; /* Ocultar todas las imágenes por defecto */
  object-fit: cover;
}
.modal-imagen{
  margin: 0 auto;
  width: 10rem;
  height: 10rem;
}
.modal-imagen img{
  width: 100%;
  object-fit: cover;
}
/* Estilo para los botones de navegación */
.anterior,
.siguiente {
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  padding: 8px;
  background-color: #f1f1f1;
}

.anterior {
  left: 0;
}

.siguiente {
  right: 0;
}

#whatsapp {
  z-index: 5;
  position: fixed;
  bottom: 20px;
  right: 20px;
}
svg {
  color: white;
  width: 80px;
  filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.4));
}
circle {
  fill: #25d366;
}
path {
  fill: #fff;
}
#whatsapp:hover::before {
  background-color: white;
  color: black;
  font-weight: 400;
  border-radius: 0.5rem;
  position: absolute;
  border: 2px solid #25d366;
  content: "👋 Hola soy Liliana, ¿En qué puedo ayudarte?";
  width: 20rem;
  height: 8rem;
  top: 0;
  right: 110px;
  text-align: center;
  padding-top: 20px;
}
#whatsapp:hover::after {
  content: "";
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
  background-color: #25d366;
  position: absolute;
  width: 3rem;
  height: 3rem;
  right: 80px;
  top: 25px;
}

.header {
  background-image: linear-gradient(
      to right,
      transparentize(v.$gris, 0.1) 0%,
      transparentize(v.$primario, 0.1) 100%
    ),
    url(../img/header-fondo.png);
  background-size: cover;
  background-position: center right;
  padding: 2rem 0;
  position: relative;
  @include m.tablet {
    padding: 3rem 0 20rem 0;
    max-height: 50rem;
  }
  &__principal {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__principal h3 {
    display: none;
    color: white;
    font-size: 5rem;
    @include m.tablet {
      font-size: 5rem;
    }
    @include m.desktop {
      display: block;
      font-size: 10rem;
    }
  }
  &__principal .txt-azul {
    color: #4484bc;
  }

  &__barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__logo {
    width: 15rem;
  }
  &__contenido {
    @include m.tablet {
      margin-top: 10rem;
      max-width: 50rem;
    }
  }
  &__heading {
    color: v.$primario;
    text-align: center;
    animation: move 2s ease-in 0s 1 normal backwards;
    @include m.tablet {
      text-align: left;
    }
  }
  &__texto {
    color: v.$primario;
    display: none;
    animation: move 2s ease-in 0s 1 normal backwards;
    @include m.tablet {
      display: block;
    }
  }
  @keyframes move {
    0% {
      opacity: 0;
      transform: translateX(-50px);
    }

    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
  &__imagen {
    display: none;
    @include m.tablet {
      transform: scaleX(-1);
      display: block;
      position: absolute;
      right: 0;
      bottom: -5rem;
      max-width: 30rem;
      border-top-right-radius: 2rem;
      border-bottom-right-radius: 5rem;
    }
    @include m.desktopXL {
      max-width: 50rem;
    }
  }
}
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  @include m.tablet {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
  &__link {
    color: v.$blanco;
    font-weight: 900;
    font-size: 1.8rem;
    margin-bottom: 2rem;
    padding-bottom: 1px;
    &:hover {
      color: v.$primario;
      transition: 0.3s ease-in;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
    @include m.tablet {
      margin-bottom: 0;
    }
  }
}
