@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros{
    height: auto;
    padding: 2rem 0;
    background-image: linear-gradient(to right, transparentize(v.$gris, .1) 0%, transparentize(v.$primario, .1) 100%), url(../img/header-fondo.png);
    background-size: cover;
    background-position: center;
}

.contenedor{
    &__flex{
        width: 80%;
        margin: 5rem auto;
        min-height: 40rem;
        @include m.tablet{
            display: flex;
            flex-direction: row-reverse;
            gap: 2rem;
            margin-bottom: 2rem;
            min-height: 20rem;
            justify-content: center;
            align-items: center;
        }

    }
    &__flex:nth-child(2){
        flex-direction: row;
    }
    &__texto{
        flex: 2;

    }
    &__texto p{
        text-align: center;
        @include m.tablet{
            text-align: left
        }
    }
    &__titulo{
        font-size: 2rem;
        @include m.heading;
        @include m.tablet{
            font-size: 3rem;
        }
    }
    &__imagen{
        flex: 1;
        img{
            width: 100%;
            object-fit: cover;
        }
        
    }

}
.logo-2{
    width: 20rem;
}