@use 'base/variables' as v;
@use 'base/mixins' as m;


.beneficios{
    &__titulo{
        margin: 2rem 0;
        @include m.heading();
        font-family: v.$fuentePrincipal;
        span{
            font-weight: 700;
        }
    }
    &__flex{
        display: flex;
        flex-direction: column;
        padding: 2rem;
        @include m.tablet{
            flex-direction: row;
        }
    }
    &__azul{
        padding: 2rem;
        min-height: 30rem;
        background: v.$primario;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    &__blanco{
        padding: 2rem;
        min-height: 30rem;
        background: v.$blanco;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    &__texto-1,&__texto-2{
        text-align: center;
        @include m.tablet{
            text-align: left;
        }
    }
    &__texto-1 p{
        color: v.$blanco;
    }
}