@use 'base/variables' as v;
@use 'base/mixins' as m;

.destacada{
    margin-top: 5rem;
    // background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, v.$primario 50%, v.$primario 100%), url(../img/img-destacada.jpg);
    // background-size: 100%, auto auto; //Le doy las propiedades al linear gradient y a la url
    // background-position: center, -90rem -40rem;
    // background-repeat: no-repeat;

    // @include m.tablet{
    //     background-image: linear-gradient(to right, transparent 0%, transparent 50%, v.$primario 50%, v.$primario 100%), url(../img/img-destacada.jpg);
    //     background-position: center, -50rem center;
    // }

    &__grid{
        display: flex;
        flex-direction: column;
        @include m.desktop{
            flex-direction: row;
        }
    }
    &__imagen{
        flex: 1;
    }
    &__imagen img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__contenido{
        background: v.$primario;
        padding: 2rem;
        flex: 1;
    }
    &__heading{
        color: v.$blanco;
        text-transform: uppercase;
    }
    &__texto{
        color: v.$blanco;
        font-size: 1.4rem;
        @include m.tablet{
            font-size: 1.8rem;
        }
    }
    &__precio{
        color: v.$blanco;
        font-size: 2.4rem;
        font-weight: 700;
    }
    &__enlace{
        @include m.boton(v.$blanco, v.$primario);
        display: block;
    }
}