@use 'base/variables' as v;
@use 'base/mixins' as m;


.contacto{
    height: auto;
    padding: 2rem 0;
    background-image: linear-gradient(to right, transparentize(v.$gris, .1) 0%, transparentize(v.$primario, .1) 100%), url(../img/header-fondo.png);
    background-size: cover;
    background-position: center;
}
.redes{
    margin: 2rem auto;
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    &__iconos{
        border-bottom: 2px solid transparent;
        transition: .5s ease-in-out;
        cursor: pointer;
        svg{
            width: 50px;
            transition: .5s ease-in;
        }
        path{
            fill: v.$primario;
        }
    }
    &__iconos:hover{
        border-bottom: 2px solid v.$primario;
        
        svg{
            transform: translateY(-10px);

        }
    }
}

.formulario{
    &__titulo{
        margin: 2rem 0;
        @include m.heading();
        font-family: v.$fuentePrincipal;
        font-weight: 700;
    }
    &__subtitulo{
        text-align: center;
        margin: 5rem 0;
        font-family: v.$fuentePrincipal;
        font-weight: 500;
        span{
            font-weight: 900;
        }
    }

    .redes{
        margin: 2rem auto;
        width: 70%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        &__iconos{
            border-bottom: 2px solid transparent;
            transition: .5s ease-in-out;
            cursor: pointer;
            svg{
                width: 50px;
                transition: .5s ease-in;
            }
            path{
                fill: v.$primario;
            }
        }
        &__iconos:hover{
            border-bottom: 2px solid v.$primario;
            
            svg{
                transform: translateY(-10px);
 
            }
        }
    }
    &__flex{
        @include m.tablet{
            width: 90%;
            margin: 5rem auto;
            display: flex;
        }

    }
    &__imagen{
        flex: 1;
        background-color: #3B4862;
        background-image: url(/build/img/img-contacto.jpg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    &__datos{
        padding: 2rem;
        flex: 1;
        display: flex;
        gap: 2rem;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    &__inputs{
        margin-bottom: 2rem;
        input{
            width: 100%;
            border: none;
            border-bottom: 2px solid v.$primario;
            padding: 1rem;
            text-align: center;
            outline: none;
        }
        input:focus{
            background-color: white;
        }
    }
    &__txtArea{
        width: 80%;
        margin: 0 auto;
        textarea{
            width: 100%;
            border: 2px solid v.$primario;
            resize: none;
            outline: none;
        }
    }
    &__enviar{
        margin: 0 auto;
        cursor: pointer;
        @include m.boton(v.$primario, white);
        display: block;
    }
}
.logo-2{
    width: 20rem;
}